import moment from 'moment';
import React from 'react';
import { sampleData } from '../../sampleData/sampleEvents';
import './style.scss';
import logo from '../../assets/img/sample/sampleEventThumb.png';
import { Link } from 'react-router-dom';

class Home extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      sampleData: sampleData,
      months: []// use in the render
    };
  }

  componentDidMount() {
    console.log(this.state.sampleData);

    let eventList = this.state.sampleData;
    eventList.sort((a, b) => (new Date(a.startTime).getTime() - new Date(b.startTime).getTime()))
    let months = [];//declare array

    eventList.map(event => {
      event.start = new Date(event.startTime);
      event.startDate = moment(event.start).format("dddd, MMMM Do YYYY");
      event.eventStart = moment(event.start).format("hh:mm");
      if (!months.includes(event.startDate)) //check the months array have or not that data
        months.push(event.startDate);// push data into the months array.
    })

    this.setState({ sampleData: eventList, months: months }, () => {
      localStorage.setItem('months', JSON.stringify(months))
      console.log(this.state.sampleData);
    })
    // if this.state.sampleData:[], setState can set or get data from sampleEvents

  }

  // ascOrder(){
  //   this.state.sampleData.sort(function(a,b){
  //     return a.startTime-b.startTime
  //   })
  // }

  render() {
    return (
      <div className='home'>
        <div className='title'>Events</div>
        {this.state.months.map((month, j) => {
          let currentMonthEvents = this.state.sampleData.filter(e => e.startDate === month);// new array
          console.log(currentMonthEvents)
          let event = []//new array
          event.push(
            <div className='date-event'>{month}</div>
          )
          console.log(event);
          event.push(
            <div className='date-event'>
              {currentMonthEvents.map((item, i) => {
                item.eventTime = `${item.eventStart} - ${item.location}`
                return (
                  <div key={i} className="event-card"><Link to='/module?id=1'>
                    <div className='event-card-detail'>
                      <div className='event-title'>{item.title}</div>
                      <div className='datetime'>{item.eventTime}</div>
                    </div>
                    <div className='event-card-img'><img src={logo} /></div>
                  </Link>
                  </div>
                )
              }
              )}
            </div>)
          console.log(event)
          console.log(currentMonthEvents)
          return <div key={j}>{event}</div>
        }
        )
        }
      </div>
    )
  }
}

export default Home;

